function footerRtl() {

  var x = window.matchMedia("(max-width: 800px)")

  if (x.matches) { 
  document.querySelector('.footer').style.margin = "0 -5vw" 
  }

}

        
function footerLtr() {

  var x = window.matchMedia("(max-width: 800px)")

  if (x.matches) { 
  document.querySelector('.footer').style.margin = "0 5vw" 
  }

}